    margin-bottom: 25px;
  }
  .popup__text {
    padding: 0 200px;
  }
  .popup__button-wrap {
    background-color: #f2f2f2;
  }
  .popup__button--success {
    width: 460px;
  }
}
.footer {
  background-color: #ffffff;
}
.footer--index {
  display: none;
}
.footer__wrapper {
  padding: 40px 50px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.footer__social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.social__item {
  position: relative;
  display: block;
  width: 45px;
  height: 45px;
  color: #d22856;
  border: 3px solid rgba(40, 54, 69, 0.1);
  border-radius: 50%;
}
.social__item:hover {
  border-color: #283645;
}
.social__item:active {
  color: rgba(210, 40, 86, 0.3);
  border-color: rgba(40, 54, 69, 0.1);
}
.social__item-image {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.footer__copyright-link {
  position: relative;
  display: flex;
  align-items: center;
  width: 130px;
}
.footer__copyright-text {
  padding-right: 15px;
  font-size: 14px;
  line-height: 18px;
  color: #283645;
}
.footer__copyright-image {
  color: #d22856;
}
.footer__copyright-image:hover {
  color: #283645;
}
.footer__copyright-image:active {
  opacity: 0.3;
}
@media (min-width: 660px) {
  .footer--index {
    display: flex;
  }
  .footer__wrapper {
    width: 620px;
    height: 127px;
    padding: 0 20px;
    margin: 0 auto;
    flex-direction: row;
    justify-content: space-between;
  }
  .footer__logo {
    margin-bottom: 10px;
  }
  .footer__social {
    padding-top: 15px;
  }
  .footer__copyright {
    padding-top: 15px;
    margin-left: 20px;
  }
}
@media (min-width: 960px) {
  .footer__wrapper {
    width: 940px;
    height: 197px;
    padding: 0 10px;
  }
  .footer__logo {
    margin-bottom: 15px;
  }
  .footer__social {
    justify-content: center;
    width: 300px;
    padding: 0;
    margin: 0;
  }
  .social__item {
    margin-left: 11px;
    margin-bottom: 3px;
  }
  .footer__copyright {
    margin: 0;
    padding: 0;
  }
  .footer__copyright-link {
    width: 155px;
  }
  .footer__copyright-text {
    font-size: 18px;
  }
}
